import React, { useState } from 'react'

export const CardProduct = ({ data }) => {
    const [save, setSave] = useState(false)
    const saveCard = () => {
        save === false ? setSave(true) : setSave(false);
    }
    return (
        <div className={save === false ? 'img-card-product' : 'img-card-product border'}>
            <img src={data.imgUrl} />
            <p>{data.name}</p>
            <p><span>{data.price}</span></p>
            <button onClick={saveCard} className={save === false ? "save-card" : "save-card button-bg"}>
                Guardar
            </button>
        </div >
    )
}
