import React from 'react'
import { DATA_PRODUCTS } from '../utils/util';
import { CardProduct } from './CardProduct';

const Products = () => {
    const dataInfo = DATA_PRODUCTS;
    const [item] = dataInfo;
    const { items } = item;
    return (
        <div className="info-container">
            <div className="info-title">
                <h2>Todos nuestros <span>productos</span></h2>
            </div>
            <div className="info-section-container">
                <div className="info-text">
                    <p>
                        En esta sección podrás seleccionar de todos los productos que tenemos disponibles
                        los que más te gusten y poder solicitar un presupuesto final.
                    </p>
                </div>
                <div className="info-section-container">
                    <div className="info-img-container-featured">
                        {
                            items.map(card => (
                                <CardProduct key={card} data={card} />
                            ))
                        }
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Products;